import { FC, useState } from "react";
import { typeToDo_cur } from "../../Type/typeToDo";
import { useToDo } from "../../Hooks/hooks";



const ToDo_cur:FC<typeToDo_cur>=({todo,change_textE,change_doneE})=>{
    



    const {value,error,is_change,onChange,onBlur,ondblclick} = useToDo(todo.text,change_textE)

    // const [isDone,setDone] =useState<boolean>(todo.isDone)
    // console.log(isDone);
    
    const checkbox_change=()=>{
        change_doneE()
        // setDone(!isDone)
    }




    return <div style={{display:"flex"}} className={'box '+(todo.isDone?'done':'not_done')}>
        <input type="checkbox"  className="check" checked={todo.isDone} onChange={checkbox_change} />
        {is_change?
        <input type="text" value={value} onChange={onChange} onBlur={onBlur} autoFocus/>
        :
        <p onDoubleClick={ondblclick}>
            {value}
        </p>
        }
        <button>
            X
        </button>
        </div>
}

export default ToDo_cur