import { conncect_type, connect_type_error, connect_type_ok } from '../Type/help_type'
import {country} from '../Type/typeCountries'


    
    export const connect = (json: any) :conncect_type<country> => {
    // if(json.status === 200) return {data : json.data , status : json.status};

    const regxp_api = /.{1,9}\/\/|\..{1,10}/gi
    const api: string = json.config.baseURL
    // console.log(json);
    
    


    if (json.response) {
        
        return {
            message: json.response.data.message,
            status: json.response.status,
            request:json.config.url,
            api: api.replace(regxp_api, '')
        }
    }
    else {
        return {
            status: json.status,
            data: json.data
        }
    }


}