import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux"
import { NavLink } from "react-router-dom";
import { useAppDispatch } from "../../Hooks/helps";
import { changeInput, getCountries } from "../../Redux/Countries/Countries.redux";
import { countryForSearch } from "../../Type/typeCountries";
import C from "./Countries.module.css"

const Countries = () => {
    const dispatch = useAppDispatch()
    const { input, countries } = useSelector((state: any) => state.countries)



    const changeInputE = (e: React.ChangeEvent<HTMLInputElement>) => {
        dispatch(changeInput(e.target.value))
        dispatch(getCountries())
        console.log(e.target.value);
        
    }







    return <div className={C.all_cont}>
        <input className={C.input} type='text' onChange={changeInputE} value={input} />
        <div className={C.countries_cont}>
            {countries.map((e: countryForSearch) => <NavLink to={'/country/'+e._id} key={e._id}><div className={C.country_cont} key={e.name}>
                <p>{e.name}</p>
                <img className={C.flag} src={e.flag} />
            </div>
            </NavLink>
            )}
        </div>
    </div>
    
}



export default Countries