import { AnyAction, createAsyncThunk, createSlice, AsyncThunkAction, PayloadAction } from "@reduxjs/toolkit"
import {debounce} from 'ts-debounce'
import { countries_api } from "../../api/countries"
import {  conncect_type } from "../../Type/help_type"
import { Countries_state, country, countryAnswer, countryForSearch } from "../../Type/typeCountries"
import { onError } from "../Error/Error.redux"


const isError = (action : AnyAction )=>{
  return action.type.endsWith('rejected')
  }
