import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { connect_type_error } from "../../Type/help_type";
import { Error_state } from "../../Type/typeError";


interface stateError  {
    status : string,request : string
} 

const initialState : Error_state = {
    data: [],

}
//todo : interface state

const ErrorSlice= createSlice({
    name:'Error',
    initialState,
    reducers:{
        onError(state,action:PayloadAction<connect_type_error>){
            state.data.push(action.payload)
            // console.log(action.payload);
            
            // state.request 
            // for (const i in action.payload) {
            //     if(i!='data'){
            //         state[i]=action.payload[i]
            //     }
            // }
            
            
        }
    }
})

export const {onError} = ErrorSlice.actions 

export default ErrorSlice.reducer