import { applyMiddleware, configureStore } from "@reduxjs/toolkit";
import logger from "redux-logger";

import CountriesRedux from "./Countries/Countries.redux";
import ErrorRedux from "./Error/Error.redux";
import ErrorSlice from "./Error/Error.redux";
import SettingsRedux from "./Settings/Settings.redux";
// import Settings from "./Settings/Settings";
import ToDoRedux from "./ToDo/ToDo.redux";
// Setting
// const middlewareEnhancer = applyMiddleware(asyncFunctionMiddleware)
const asyncFunctionMiddleware = (storeAPI:any) => (next:any) => (action:any) => {
    // If the "action" is actually a function instead...
    console.log('asd');
    
    if (typeof action === 'function') {
      // then call the function and pass `dispatch` and `getState` as arguments
      return action(storeAPI.dispatch, storeAPI.getState)
    }
  
    // Otherwise, it's a normal action - send it onwards
    return next(action)
  }

const middlewars =applyMiddleware(asyncFunctionMiddleware)


const store = configureStore({
    reducer : {
        todo : ToDoRedux,
        setings:SettingsRedux,
        countries : CountriesRedux,
        error:ErrorRedux,
    },
    //  middleware:(asyncFunctionMiddleware) => asyncFunctionMiddleware().concat(logger)
})

export default store


export type RootState = ReturnType<typeof store.getState>
export type AppDispatch = typeof store.dispatch
