
import axios from 'axios'
import { log } from 'console';
import { useAppDispatch } from '../Hooks/helps';
import { conncect_type } from '../Type/help_type';
import { country, countryAnswer, countryForSearch } from '../Type/typeCountries';
import { connect } from './helps_api';
const instons = axios.create({
    baseURL: 'https://restcountries.com/v2'
})

export const countries_api = {
    get_countries: (input: string) => instons.get('/name/' + input)
        .catch(e => e)
        .then(connect)
        .then((connect : conncect_type<country>): conncect_type<countryForSearch> => {
            console.log(connect);
            
            if (connect.status === 200) {
                connect.data =  connect.data.map((e: country): countryForSearch => ({ name: e.name, flag: e.flag, _id:e.alpha3Code }))
                // return {
                //     ...connect,
                //     data: connect.data.map((e: country): countryForSearch => ({ name: e.name, flag: e.flag }))
                // }
            } 
            // else {
                return connect
            // }
        }),
    get_country:(id:string|undefined) => instons.get('/alpha/' + id)
    .catch(e=>e)
    .then(connect)
    .then((connect: conncect_type<countryAnswer>)=>{
        // console.log(connect);
        if (connect.status === 200) {
            // console.log(1);
            connect.data =  { 
                name: connect.data.name,
                flags: connect.data.flags,
                alpha2Code:connect.data.alpha2Code,
                capital:connect.data.capital,
                timezones:connect.data.timezones,
                population:connect.data.population,
                borders:connect.data.borders,
                translations:connect.data.translations
                }
        } 
        
        
        // console.log(connect);
        
        return connect
    })
}

