import { useEffect } from "react"
import { useParams } from "react-router-dom"
import { useAppDispatch, useAppSelectors } from "../../../Hooks/helps"
import { getCountry } from "../../../Redux/Countries/Countries.redux"
import { countryAnswer } from "../../../Type/typeCountries"
import Chart from "../../Chart/Chart"
import C from "./Country.module.css"
import './Country.scss'

const Country = () => {
    const id: string | undefined = useParams().id
    const dispatch = useAppDispatch()
    const country: countryAnswer = useAppSelectors((state: any) => state.countries.country)
    console.log(country);

    // console.log(id);
    // console.log(1);
    useEffect(() => {
        // console.log(123);

        dispatch(getCountry(id))
    }, [])
    return <>
        {country ?
            <>

                <div className={C.country}>
                    <h2 className={C.name}>
                        {country.name}

                    </h2>
                    <div className={C.country_content}>
                        <div>
                            <p>Capital: {country.capital};</p>
                            <p>Code: {country.alpha2Code};</p>
                            <p>Population: {country.population};</p>
                        </div>
                        <div>
                            <h4>Neighbors</h4>
                            <ul>
                                {country.borders.map(e => <li key={e}>{e}</li>)}
                            </ul>
                        </div>
                        <div>
                            <h4>Time Zones</h4>
                            <ul>
                                {country.timezones.map(e => <li key={e}>{e}</li>)}
                            </ul>
                        </div>
                        


                    </div>
                    <div  className="C_country_img">
                        <img src={country.flags.png} />
                    </div>
                    <Chart/>
                </div>

                {/* //todo style all \\ mexin scss   */}
            </> :
            <p>Loading...</p>}
    </>
}


export default Country