import { createSlice, Slice } from "@reduxjs/toolkit"
import { log } from "console"
import shortid from "shortid"
import { slice } from "../../Type/help_type"
import {  todos, ToDo_state } from "../../Type/typeToDo"




const initialState:ToDo_state  = {
    data : [
        {
            isDone : true,
            text : 'string',
            _id:shortid()
        },
        {
            isDone : true,
            text : 'st2ring',
            _id:shortid()
        },
        {
            isDone : false,
            text : 'string',
            _id:shortid()
        },
        {
            isDone : true,
            text : 'st2ring',
            _id:shortid()
        },
        {
            isDone : false,
            text : 'string',
            _id:shortid()
        },
        {
            isDone : true,
            text : 'st2ring',
            _id:shortid()
        },
        {
            isDone : false,
            text : 'string',
            _id:shortid()
        },
        {
            isDone : true,
            text : 'st2ring',
            _id:shortid()
        },
        {
            isDone : false,
            text : 'string',
            _id:shortid()
        },
        {
            isDone : true,
            text : 'st2ring',
            _id:shortid()
        }
        
    ],
    loading : false,
    error : null,
    input : ''
}


const ToDoSlice  = createSlice({
    name : 'ToDo',
    initialState,
    reducers : {
        onAdd(state){
            state.data.push( {
                isDone:false,
                text:state.input,
                _id:shortid()
            })
            console.log(state);
            
            state.input = '';
            // setTimeout(()=>{
            //     state.input = '99';

            // },2000)
        },
        changeInput(state,action){
            state.input = action.payload;
            
        },
        change_text(state,action){
            const {_id,text}=action.payload
            const todo :any = state.data.find(e=>e._id==_id)
            todo.text=text
        },
        change_done(state,action){
            // console.log(action.payload);
            
            const _id=action.payload
            const todo :any = state.data.find(e=>e._id ==_id)
            todo.isDone = !todo.isDone
        }
    }
})

export const {onAdd,changeInput,change_text,change_done} = ToDoSlice.actions

export default ToDoSlice.reducer 




