import { setHeader } from "./Settings/Settings.redux"

let n: number = 12

export const connect = (arg: any) => (dispatch: any) => {

  let swipe_start_x: number
  let swipe_end_x: number
  const doc_width: number = window.innerWidth
  const swipe_mobile = (start: number, end: number) => {
    console.log(start);
    console.log(end);


    // console.log(n);
    // n = 22;

    console.log(doc_width);
 
    if (  Math.abs(end - start)>= (doc_width / 6)) {
      if ( (doc_width/4)<=end ) {
        // console.log(end);
        dispatch(setHeader(true))
      }
      console.log(end);
      
      if((doc_width/4)>=end){
        dispatch(setHeader(false))
      }
    }




  }
  document.addEventListener('touchstart', (e: any) => {
    // console.log(e);
    // console.log(e.touches[0].clientX);

    swipe_start_x = e.touches[0].clientX
  })
  document.addEventListener('touchend', (e: any) => {
    console.log(e);
    swipe_end_x = e.changedTouches[0].clientX
    swipe_mobile(swipe_start_x, swipe_end_x)
  })

}