import { useSelector } from "react-redux"
import { useAppSelectors } from "../../Hooks/helps"
import { error } from "../../Type/typeError"
import C from './Error.module.css'


const Error = () => {

    const { data } = useAppSelectors((state: any) => state.error)
    console.log(data);
    

    return <>

        {data.length ?
            <div className={C.error_cont}>
                {data.map((e: error) =>
                    <div className={C.error}>
                        <p>
                            Error{e.status}
                        </p>
                        <p>
                            {e.message}
                        </p>
                    </div>)}
            </div>

            : <p></p>

        }
    </>
}




export default Error