import React, { useState } from "react"

export const useToDo = (val:string,funForOnBlur :any)=> {
    const [value,setValue] = useState<string>(val)
    const [error,setError] = useState(null)
    const [is_change,setChange]=useState(false)

    return {
        value,
        error,
        is_change,
        onChange : (e : React.ChangeEvent<HTMLInputElement>) => setValue(e.target.value),
        ondblclick:()=>{setChange(true)},
        onBlur : () => {
            
            funForOnBlur(value)
            setChange(false)
        }
    }
}