// import { log } from "console"
import React, { FC, ReactNode, useMemo, useState } from "react"
import shortid from "shortid"
import ToDo_cur from "./ToDO_cur"
// import shortid from "shortid"
import { todos } from "../../Type/typeToDo"
import { useDispatch, useSelector } from "react-redux"
import { changeInput, onAdd,change_text,change_done } from "../../Redux/ToDo/ToDo.redux"
import { store } from "../../Type/store"

import './ToDo.scss'






const ToDo: FC = ( ) =>{
    
    const dispatch = useDispatch()
   
    const {input,data} = useSelector((state:store)=>state.todo)
    
    
    const onInput=(e:React.ChangeEvent<HTMLInputElement>):void=>{
        dispatch(changeInput(e.target.value))
    }

    const change_textE=(_id:string)=>(text:string)=>{dispatch(change_text({text,_id}))}
    const change_doneE=(_id:string)=>()=>dispatch(change_done(_id))
    
    const onAddE=():void=>{
        
        dispatch(onAdd())
        
    }
    console.log(data);
    
    const list_done_memo= useMemo(
        () : ReactNode[] => data.map( (elem : todos) => elem.isDone &&<ToDo_cur key={elem._id} todo={elem} change_textE={change_textE(elem._id)} change_doneE={change_doneE(elem._id)}/>   )
        ,[data]
    )
    const list_not_done_memo= useMemo(
        () : ReactNode[] => data.map( (elem : todos) => !elem.isDone &&<ToDo_cur key={elem._id} todo={elem} change_textE={change_textE(elem._id)} change_doneE={change_doneE(elem._id)}/>   )
        ,[data]
    )
    return <div className="todo_all">
        <div>
            <input type="text" onInput={onInput} value={input}/>
            <button onClick={onAddE}>Add</button>
        </div>
        <div className="todos_cont">
            
            <div className="not_done_container">
                {list_not_done_memo}
            </div>
            <div className="done_container">
            {list_done_memo}
            </div>
        </div>
    </div>

}


export default ToDo